.mainApp{
  position: relative;
  width: 100vw;
  height: 100vh;
}
.centered{
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.rowStyle{
  display: flex;
  flex-direction: row;
}

.mainText{
  font-size: 6rem;
  font-weight: 900;
}

.mediumText{
  font-size: 1.5rem;
  font-weight: 400;
  text-transform: uppercase;
}

.smallText{
  text-align: center;
  color: #6a6a6a;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
}

.icon{
  background-color: transparent;
  width: 2.5rem;
  margin: 5px;
  cursor: pointer;
}

.logo{
  width: 200px;
}

.footer{
  position: fixed;
  left: 50%;
  bottom: 20px;
  transform: translate(-50%, -50%);
  margin: 0 auto;
}